import React, { useState, useEffect, useRef, useReducer } from 'react'
import ReactDOM from 'react-dom'

import * as d3 from 'd3'

import { TextField, FormControlLabel, Checkbox } from "@mui/material"
import SortByAlphaIcon from '@mui/icons-material/SortByAlpha'
import { CheckboxToggle, SearchAutoComplete, FilterGroup } from './styled'

import LazyLoad from '../../feat/LazyLoad'

import { limitString } from '../../utils/format'

import { init_d3 } from '../../utils/plot'

import { ms_per_day } from '../../mods/a_utils'

export default function CombinedPlot({
  base_results,
  new_results,
  base_resource_distribution,
  new_resource_distribution,
  preset,
  filter,
  width,
  height = 610,
}) {

  const [showBaselinePlot, setShowBaselinePlot] = useState(true)
  const [showNewPlot, setShowNewPlot] = useState(true)
  const [searchValue, setSearchValue] = useState(null)
  const [toggleSort, setToggleSort] = useState(false)

  const [selectAll, setSelectAll] = useState(true)

  const ref = useRef()
  // let resources = new_results
  const [resources, setResources] = useState(new_results)
  const [baseResources, setBaseResources] = useState(base_results)

  // console.log(baseResources[1].distribution.xy)
  // console.log(resources[1].distribution.xy)

  const [_, forceUpdate] = useReducer((x) => x + 1, 0)

  useEffect(() => {
    if (!new_resource_distribution) return

    const x_optimized_domain = d3.extent(new_resource_distribution.map((r) => { return parseInt(r.x) }))
    const x_baseline_domain = d3.extent(base_resource_distribution.map((r) => {
      return parseInt(r.x)
    }))

    const startingDate = new Date(x_optimized_domain[0])
    const endingDate = new Date(x_optimized_domain[1])

    const diffTime = Math.abs(endingDate - startingDate)
    const diffDays = Math.ceil(diffTime / (1000 * 60 * 60 * 24));

    const x_domain = [x_optimized_domain[0], new Date(endingDate.setDate(endingDate.getDate() + (diffDays / 10)))] // padding at end of resources chart
    const y_optimized_values = (new_resource_distribution).map((r) => {
      return parseInt(r.y)
    })

    const y_baseline_values = (base_resource_distribution).map((r) => {
      return parseInt(r.y)
    })

    const y_baseline_domain = d3.extent(y_baseline_values.flat())
    const types = ['Labor_data']

    const y_domain = y_baseline_domain

    const { svg, x_scale, y_scale } = init_d3({
      x_domain,
      y_domain,
      width,
      height,
      ref,
      x_type: d3.scaleUtc,
      y_type: d3.scaleLinear,
      types
    })

    let x = 0
    const rect_all = svg.append('g').attr('class', 'clip')
    for (const r in baseResources) {
      const rect_base = rect_all.append('g')
      rect_base
        .selectAll('rect')
        .data(Object.entries(baseResources[r].distribution.xy))
        .join('rect')
        .attr('class', 'Labor_data')
        .attr('x', (d) => {
          return x_scale(d[0])
        })
        .attr('width', (d) => {
          return x_scale(ms_per_day) - x_scale(0) - ((x_scale(ms_per_day) - x_scale(0)) * 0.1) - ((x_scale(ms_per_day) - x_scale(0)) * 0.1)
        })
        .attr('y', (d) => y_scale(d[1].data))
        .attr('height', (d) => {
          return y_scale(y_domain[0]) - y_scale(d[1].data)
        })
        .attr('fill', (d, i) => {
          if (resources[r].show_in_plot) {
            return '#E3E3E3'
          }
          else
            return 'none'
        })
        .attr('display', showBaselinePlot ? 'block' : 'none')
        .on('mouseover', (event, d) => {
          // rect_base.selectAll(".rect").attr("opacity", 1);
          const startDate = Object.keys(baseResources[r]?.distribution?.xy)[0]
          const endDate = Object.keys(baseResources[r]?.distribution?.xy).at(-1)
          const tooltip = (
            <div title={baseResources[r].name} style={{
              background: '#F0F1F3',
              border: '1px solid #B5B5B5',
              borderRadius: '8px',
              position: 'absolute',
              top: event.pageY,
              left: event.pageX + 10,
              padding: '6px 8px',
              pointerEvents: 'none',
              borderRadius: '8px'
            }}>
              {/* {new Date(parseInt(startDate)).toString().slice(4, 15)} - {new Date(parseInt(endDate)).toString().slice(4, 15)}<br /> */}
              {baseResources[r].name}
            </div>
          )
          ReactDOM.render(tooltip, document.getElementById('tooltip'))
        })
        .on('mouseout', () => {
          ReactDOM.unmountComponentAtNode(document.getElementById('tooltip'))
        })
      x++
    }

    x = 0

    for (const r in resources) {
      const rect = rect_all.append('g')
      rect
        .selectAll('rect')
        .data(Object.entries(resources[r].distribution.xy))
        .join('rect')
        .attr('class', 'Labor_data')
        .attr('x', (d) => {
          return x_scale(d[0])
        })
        .attr('width', (d) => {
          return x_scale(ms_per_day) - x_scale(0) - ((x_scale(ms_per_day) - x_scale(0)) * 0.1) - ((x_scale(ms_per_day) - x_scale(0)) * 0.1)
        })
        .attr('y', (d) => y_scale(d[1].data))
        .attr('height', (d) => {
          return y_scale(y_domain[0]) - y_scale(d[1].data)
        })
        .attr('display', showNewPlot ? 'block' : 'none')
        .attr('fill', (d, i) => {
          if (resources[r].show_in_plot) {
            return resources[r].color
          }
          else
            return 'none'
        })
        .on('mouseover', (event, d) => {
          // const sort = Object.keys(resources[r]?.distribution?.xy).sort((a, b) => a.last_nom - b.last_nom)
          // console.log(Object.values(sort).map((i) => new Date(parseInt(i)).toString().slice(4, 15)))
          const startDate = Object.keys(resources[r]?.distribution?.xy)[0]
          const endDate = Object.keys(resources[r]?.distribution?.xy).at(-1)
          const tooltip = (
            <div title={resources[r].name} style={{
              background: '#35383E',
              border: '1px solid #B5B5B5',
              borderRadius: '8px',
              color: '#FFFFFF',
              position: 'absolute',
              top: event.pageY,
              left: event.pageX + 10,
              padding: '6px 8px',
              pointerEvents: 'none',
              borderRadius: '8px'
            }}>
              {/* {new Date(parseInt(startDate)).toString().slice(4, 15)} - {new Date(parseInt(endDate)).toString().slice(4, 15)}<br /> */}
              {resources[r].name}
            </div>
          )
          ReactDOM.render(tooltip, document.getElementById('tooltip'))
        })
        .on('mouseout', () => {
          ReactDOM.unmountComponentAtNode(document.getElementById('tooltip'))
        })
      x++
    }

  }, [new_resource_distribution, _, showNewPlot, showBaselinePlot, selectAll, resources, baseResources])

  const toggleSelectResource = (r) => {
    resources.filter((item) => item.id === r).map((newItem) => {
      return newItem.show_in_plot = !newItem.show_in_plot
    }
    )
    baseResources.filter((item) => item.id === r).map((newItem) => {
      return newItem.show_in_plot = !newItem.show_in_plot
    }
    )
    forceUpdate()
  }

  useEffect(() => {
    setResources(resources.map((newItem) => ({
      ...newItem,
      show_in_plot: selectAll
    }
    )))
    setBaseResources(baseResources.map((newItem) => ({
      ...newItem,
      show_in_plot: selectAll
    }
    )))
  }, [selectAll])

  if (!new_resource_distribution) return null

  // loading x resources
  const [visibleCount, setVisibleCount] = useState(Object.values(resources).length > 50 ? 56 : 35)
  const remainingLength = Object.values(resources).length - visibleCount
  const handleLoadMore = () => {
    setVisibleCount(prevCount => prevCount + remainingLength)
  }

  // search data
  const searchData = Object.values(resources)
    .filter(r => r.type === filter)
    .slice(0, visibleCount)
    .map((r, i) => {
      if (r.name === undefined) {
        return ' '
      }
      return r.name
    })

  return (
    <>
      <CheckboxToggle>
        <FormControlLabel control={<Checkbox checked={showNewPlot} onClick={() => setShowNewPlot(!showNewPlot)} />} label="Show Optimized" />
        <FormControlLabel control={<Checkbox checked={showBaselinePlot} onClick={() => setShowBaselinePlot(!showBaselinePlot)} />} label="Show Baseline" />
      </CheckboxToggle>

      <svg ref={ref}></svg>
      <div id="tooltip" />

      {resources ? <>
        <div className="row"><h4>{filter} Legend</h4></div>
        <FilterGroup>
          <div
            className={`button ${selectAll ? 'active' : ''}`}>
            <label htmlFor="select"><Checkbox checked={selectAll} id="select" name="select" onChange={(e, checked) => setSelectAll(checked)} />
              {selectAll ? 'Deselect All' : "Select All"}</label>
          </div>
          <div className={`button ${toggleSort ? "active" : ""}`} onClick={() => setToggleSort(!toggleSort)}> <SortByAlphaIcon /> Sort</div>
          <SearchAutoComplete
            id="resources-search"
            value={searchValue}
            onChange={(e, newValue) => setSearchValue(newValue)}
            options={searchData}
            renderInput={(params) => <TextField {...params} label="Type to search..." />}
          />
        </FilterGroup >
        <div className="legend">
          {Object.values(resources)
            .filter(r => r.type === filter)
            .sort((a, b) => {
              // need to figure out the colours in line 258
              if (toggleSort) return a.name.localeCompare(b.name)
            })
            .filter(r => {
              if (searchValue) return r.name === searchValue
              if (searchValue === null) return r
            })
            .slice(0, visibleCount)
            .map((r, i) => {
              return <div
                key={r + ' ' + i}
                className="item resources"
                style={{
                  opacity: r?.show_in_plot ? '100%' : '50%',
                  cursor: 'pointer'
                }}
                onClick={(e, i) => {
                  toggleSelectResource(r.id)
                }}
              >
                <div
                  style={{
                    width: '14px',
                    height: '14px',
                    background: r?.color,
                    marginRight: '5px',
                    borderRadius: '50%',
                  }}
                ></div>
                <div>{limitString(r.name, 20)}</div>
              </div>
            }
            )}
          {Object.values(resources).filter(r => r.type === filter).length > visibleCount && (
            <LazyLoad onLoadMore={handleLoadMore}>
              <button>Load More</button>
            </LazyLoad>
          )}
        </div>
      </>
        : ''}
    </>
  )
}
