import { domToJpeg } from 'modern-screenshot'

export function downloadImage(class_name){
  console.log(class_name);
    domToJpeg(document.querySelector(`.${class_name}`)).then(dataUrl => {
        const link = document.createElement('a')
        link.download = `${class_name}.jpg`
        link.href = dataUrl
        link.click()
      })

}
