import * as d3 from 'd3'
import { ms_per_day } from '../mods/a_utils'
export function init_d3({
  margin = 50,
  x_domain,
  y_domain,
  width,
  height,
  ref,
  x_label,
  y_label,
  x_type = d3.scaleLinear,
  y_type = d3.scaleLinear,
  types,
  name
}) {
  const x1 = margin,
    x2 = width - margin,
    y2 = margin,
    y1 = height - margin,
    x_scale = x_type(x_domain, [x1, x2]),
    y_scale = y_type(y_domain, [y1, y2])

  const svg = d3.select(ref.current).attr('width', width).attr('height', height)
  svg.selectChildren().remove()
  // !Clip Path 
  svg.style('overflow', 'hidden')
  // 创建裁剪路径
  const clipPath = svg.insert('clipPath')
    .attr('id', 'chartClipPath')
    .append('rect')
    .attr('x', x1) // 设置裁剪区域的起始横坐标
    .attr('y', y1) // 设置裁剪区域的起始纵坐标
    .attr('width', x2 - x1) // 设置裁剪区域的宽度
    .attr('height', -(y2 - y1)) // 设置裁剪区域的高度
    .attr('transform', `translate(0,${y2 - y1})`)
    
  // 创建图表容器，并应用裁剪路径
  // 添加裁剪属性
  // .attr('clip-path', 'url(#chartClipPath)')
  //!end 
  const x_axis = svg.append('g')

  x_axis
    .call(d3.axisBottom(x_scale))
    .attr('transform', `translate(0, ${y1})`)
    .call((g) => {  
      g.append('text')
        .attr('x', width / 2)
        .attr('y', 40)
        .attr('fill', '#2d2d2d')
        .attr('font-size', 16)
        .attr('text-align', 'right')
        .text(x_label)
    })

  const x_axis_2 = svg.append('g')
  x_axis_2.call(d3.axisTop(x_scale).tickValues([])).attr('transform', `translate(0, ${y2})`)

  const y_axis = svg.append('g')
  y_axis
    .call(d3.axisLeft(y_scale))
    .attr('transform', `translate(${x1}, 0)`)
    .call((g) => {
      g.append('text')
        .attr('text-anchor', 'end')
        .attr('y', 30) // margin from TOP
        .attr('x', -30) // margin from TOP
        .attr('fill', '#2d2d2d')
        .attr('font-size', 16)
        .text(y_label)
        // .attr('transform', 'rotate(-90)')
        .attr('text-anchor', 'start')
    })

  const y_axis_2 = svg.append('g')
  y_axis_2.call(d3.axisRight(y_scale).tickValues([])).attr('transform', `translate(${x2}, 0)`)

  const xGrid = d3.axisBottom()
    .scale(x_scale)
    .tickFormat('')
    .tickSizeInner(height - 2 * margin)

  svg.append('g')
    .attr('class', 'x-grid')
    .attr('transform', `translate(0, ${y2})`)
    .style('opacity', 0.3)
    .style('stroke-dasharray', ('3, 3'))
    .call(xGrid)

  const yGrid = d3.axisLeft()
    .scale(y_scale)
    .tickFormat('')
    .tickSizeInner(-width + 2 * margin)

  svg.append('g')
    .attr('class', 'y-grid')
    .attr('transform', `translate(${x1}, 0)`)
    .style('opacity', 0.3)
    .style('stroke-dasharray', ('3, 3'))
    .call(yGrid)
  //! zoom
  const zoomed = (event) => {
    const { transform } = event
    // 网格线缩放
    svg.select('.x-grid').call(xGrid.scale(transform.rescaleX(x_scale)))
    svg.select('.y-grid').call(yGrid.scale(transform.rescaleY(y_scale)))
    // 坐标轴缩放
    x_axis.call(d3.axisBottom(transform.rescaleX(x_scale)))
    x_axis_2.call(d3.axisTop(transform.rescaleX(x_scale)).tickValues([]))
    y_axis.call(d3.axisLeft(transform.rescaleY(y_scale)))
    y_axis_2.call(d3.axisRight(transform.rescaleY(y_scale)).tickValues([]))
    // 元素缩放
    // 更新图表元素
    // const selectedElements = svg.selectAll(type).nodes();
    // console.log(selectedElements);
    // console.log(svg.selectAll(type).data());
    types && types.forEach(type => {
      switch (type) {
        case 'new_data':
          svg.selectAll(`.${type}`)
            .attr('cy', (d) => transform.rescaleY(y_scale)(d.rsrc_max))
            .attr('cx', (d) => transform.rescaleX(x_scale)(d.drtn_day))
          break
        case 'base_data':
          svg.selectAll(`.${type}`)
            
            .attr('cy', (d) => transform.rescaleY(y_scale)(d.output.laborDistribution.max.data))
            .attr('cx', (d) => transform.rescaleX(x_scale)(d.output.projectDurationDays.data))
          break  
        case 'plannedData':
          svg.selectAll(`.${type}`)
            .attr('x', (d) => transform.applyX(x_scale(d.plannedStart)))
            .attr('y', (d, i) => transform.applyY(y_scale(i)))  // 应用Y轴的缩放变换
            .attr('width', (d) => Math.max(transform.k * (x_scale(d.plannedFinish) - x_scale(d.plannedStart)), 0))
            .attr('height', (d) => Math.max(transform.k * (y_scale(1) - y_scale(0)) - 0.5 - 0.5, 0))  // 应用Y轴的缩放变换
    
          break
        case 'newData':
          svg.selectAll(`.${type}`)
            .attr('x', (d) => transform.applyX(x_scale(d.newStart?.data)))
            .attr('y', (d, i) => transform.applyY(y_scale(i)))  // 应用Y轴的缩放变换
            .attr('width', (d) => Math.max(transform.k * (x_scale(d.newFinish?.data) - x_scale(d.newStart?.data)), 0))
            .attr('height', (d) => Math.max(transform.k * (y_scale(1) - y_scale(0)) - 0.5 - 0.5, 0))  // 应用Y轴的缩放变换
      
          break  
        case 'Labor_data':
          svg.selectAll(`.${type}`)
       
            .attr('x', (d) => transform.applyX(x_scale(d[0])))
            .attr('y', (d, i) =>transform.applyY(y_scale(d[1].data)))  // 应用Y轴的缩放变换
            .attr('width', (d) =>Math.max(transform.k * (x_scale(ms_per_day) - x_scale(0))-1, 0))
            .attr('height', (d) => Math.max(transform.k * (y_scale(y_domain[0]) - y_scale(d[1].data)), 0))  // 应用Y轴的缩放变换

          break 
        case 'step_data' :
          svg.selectAll(`.${type}`)
            .attr('cy', (d) => {
              if (name === 'laborDistribution') {
                return transform.applyY(y_scale(d.output[name].max))
              } 
              if (name === 'loss') {
                return transform.applyY(y_scale(d.loss))
              } 
              if (name === 'criticalPath') {
                return transform.applyY(y_scale(d.output.criticalPath.length))
              }
              return transform.applyY(y_scale(d.output[name]))
            })
            .attr('cx', (d) => transform.rescaleX(x_scale)(d.idx))
          break
        case 'Resources_data':
          svg.selectAll(`.${type}`)
            .attr('x', (d) => transform.applyX(x_scale(d[0])))
            .attr('y', (d, i) =>transform.applyY(y_scale(d[1].data)))  // 应用Y轴的缩放变换
            .attr('width', (d) =>Math.max(transform.k * (x_scale(ms_per_day) - x_scale(0)), 0))
            .attr('height', (d) => Math.max(transform.k * (y_scale(y_domain[0]) - y_scale(d[1].data)), 0))  // 应用Y轴的缩放变换

          break  
        default:
          break
      }
    })
   
  }
  const zoom = d3.zoom()
    .scaleExtent([1, Infinity])
    .translateExtent([[0, 0], [width, height]])
    .on('zoom', zoomed)

  svg.call(zoom)
  svg.call(zoom.transform, d3.zoomIdentity)
  // !添加剪切元素
  setTimeout(() => {
    svg.selectAll('.clip').attr('clip-path', 'url(#chartClipPath)')
  }, 0)
  // !end
  return { svg, x_scale, y_scale }
}

export function domains_with_margin(x_domain, y_domain, x_amount, y_amount) {
  const x_margin = (x_domain[1] - x_domain[0]) * x_amount
  const y_margin = (y_domain[1] - y_domain[0]) * y_amount
  return {
    x_domain: [x_domain[0] - x_margin, x_domain[1] + x_margin],
    y_domain: [y_domain[0] - y_margin, y_domain[1] + y_margin]
  }
}