import jsPDF from 'jspdf';
import html2canvas from 'html2canvas';

export default function toPDF(ele) {
  const pdf = new jsPDF();
  const element = document.getElementById(ele);
  const pages = Math.ceil(element.clientHeight / pdf.internal.pageSize.getHeight());

  let currentPosition = 0;
  let currentPage = 1;

  const addPage = () => {
    pdf.addPage();
    currentPosition = -(currentPage * pdf.internal.pageSize.getHeight());
    currentPage++;
  };

  const capturePage = () => {
    return html2canvas(element, {
      y: currentPosition,
      windowWidth: document.documentElement.scrollWidth,
      windowHeight: pdf.internal.pageSize.getHeight(),
    }).then((canvas) => {
      const imageData = canvas.toDataURL('image/png');
      const width = pdf.internal.pageSize.getWidth();
      const height = (canvas.height * width) / canvas.width;

      pdf.addImage(imageData, 'PNG', 0, 0, width, height);

      if (currentPage <= pages) {
        addPage();
        return capturePage();
      }

      return Promise.resolve();
    });
  };

  addPage();
  capturePage().then(() => {
    pdf.save('document.pdf');
  });
}
